import React, { Component } from "react";
import "./css/ProjChilds.css";

import loader from "../img/animated/Loader.svg";
import Card from "./Card";
import DB from "../data/firebase";

class ProjHtml extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: null,
      showData: false
    };
  }

  componentDidMount() {
    DB.ref("/html")
      .once("value")
      .then(snapshot => {
        this.setState({
          data: snapshot.val(),
          showData: true
        });
      })
      .catch(error => {
        console.log(
          `Following Errors were caught when fetching data from database: ${error}`
        );
      });
  }

  render() {
    if (this.state.showData) {
      const data = this.state.data.map(d => {
        const codepen = d.codepen === "" ? true : false;
        const github = d.github === "" ? true : false;
        const website = d.website === "" ? true : false;

        return (
          <Card
            key={d.key}
            name={d.name}
            details={d.details}
            codepen={d.codepen}
            github={d.github}
            website={d.website}
            image={d.image}
            CDisabled={codepen}
            GDisabled={github}
            WDisabled={website}
          />
        );
      });
      return <div className="ProjChilds">{data}</div>;
    } else {
      return (
        <div className="ProjChilds">
          <img
            style={{ position: "fixed", top: "45%", left: "46%" }}
            src={loader}
            alt="Loading…"
          />
        </div>
      );
    }
  }
}

export default ProjHtml;
