import React from "react";
import "./css/Projects.css";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import ProjHtml from "./ProjHtml";
import ProjReact from "./ProjReact";

const Projects = props => {
  return (
    <Router>
      <div className="Projects">
        <div className="breadcrumbs">
          <ul>
            <li>
              <Link to={`${props.match.url}`}>
                <button>Html</button>
              </Link>
            </li>
            <li>
              <Link to={`${props.match.url}/react`}>
                <button>React</button>
              </Link>
            </li>
          </ul>
        </div>
        <div className="projCards">
          <Switch>
            <Route exact path={`${props.match.path}`} component={ProjHtml} />
            <Route path={`${props.match.path}/react`} component={ProjReact} />
          </Switch>
        </div>
      </div>
    </Router>
  );
};

export default Projects;
