import React from "react";
import "./css/Card.css";

const Card = props => {
  return (
    <div className="Card">
      <img src={props.image} alt="noImage" />
      <div className="details">
        <h3>{props.name}</h3>
        <p>{props.details}</p>
        <div className="links">
          <a href={props.codepen} target="_blank" rel="noopener noreferrer">
            <button disabled={props.CDisabled}>CodePen</button>
          </a>
          <a href={props.github} target="_blank" rel="noopener noreferrer">
            <button disabled={props.GDisabled}>Github</button>
          </a>
          <a href={props.website} target="_blank" rel="noopener noreferrer">
            <button disabled={props.WDisabled}>Website</button>
          </a>
        </div>
      </div>
    </div>
  );
};

export default Card;
